@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../fonts/OpenSans-Regular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: bold;
  src: url("../fonts/OpenSans-Bold.woff2") format("woff2"), url("../fonts/OpenSans-Bold.woff") format("woff");
  font-display: swap; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

* {
  padding: 0px;
  margin: 0px;
  border: 0px; }

*,
*:before,
*:after {
  box-sizing: border-box; }

a:focus,
a:active {
  outline: none; }

html {
  height: 100%;
  scroll-behavior: smooth; }

body {
  line-height: 1;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

input,
button,
textarea {
  font-family: "OpenSans", "Arial", sans-serif; }

input::-ms-clear {
  display: none; }

button {
  cursor: pointer; }

button::-moz-focus-inner {
  padding: 0;
  border: 0; }

a,
a:visited {
  text-decoration: none; }

a:hover {
  text-decoration: none; }

ul li {
  list-style: none; }

img {
  vertical-align: top; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit; }

.page {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: -webkit-min-content 1fr -webkit-min-content;
  grid-template-rows: min-content 1fr min-content;
  height: 100%;
  font-family: "OpenSans", "Arial", sans-serif; }

.container {
  box-sizing: border-box;
  min-width: 320px;
  max-width: 320px;
  margin: 0 auto;
  padding: 0 30px; }
  @media (min-width: 768px) {
    .container {
      padding: 0 50px; } }
  @media (min-width: 920px) {
    .container {
      max-width: 1050px;
      padding: 0; } }

.main {
  padding: 40px 0;
  background: #f5f4f2;
  min-width: 320px;
  height: 100%; }

@-webkit-keyframes move-in {
  0% {
    transform: translateX(100%);
    opacity: 0;
    visibility: hidden;
    z-index: -1;
    color: #fff; }
  100% {
    transform: translateX(0);
    opacity: 1;
    visibility: visible;
    z-index: 1;
    color: #000; } }

@keyframes move-in {
  0% {
    transform: translateX(100%);
    opacity: 0;
    visibility: hidden;
    z-index: -1;
    color: #fff; }
  100% {
    transform: translateX(0);
    opacity: 1;
    visibility: visible;
    z-index: 1;
    color: #000; } }

@-webkit-keyframes move-out {
  0% {
    transform: translateX(0);
    opacity: 1;
    visibility: visible;
    z-index: 1;
    color: #000; }
  80% {
    transform: translateX(-100%);
    opacity: 0;
    visibility: hidden;
    z-index: -1;
    color: #fff; }
  100% {
    transform: translateX(-100%);
    opacity: 0;
    visibility: hidden;
    z-index: -1;
    color: #fff;
    top: 79px;
    position: absolute; } }

@keyframes move-out {
  0% {
    transform: translateX(0);
    opacity: 1;
    visibility: visible;
    z-index: 1;
    color: #000; }
  80% {
    transform: translateX(-100%);
    opacity: 0;
    visibility: hidden;
    z-index: -1;
    color: #fff; }
  100% {
    transform: translateX(-100%);
    opacity: 0;
    visibility: hidden;
    z-index: -1;
    color: #fff;
    top: 79px;
    position: absolute; } }

.header {
  position: relative;
  z-index: 1; }

.header__container {
  display: grid;
  grid-template-columns: 1fr;
  padding: 0;
  position: absolute;
  overflow-x: hidden;
  left: 0;
  background: #fff;
  min-width: 100%;
  transition: height 0.3s ease 0s; }

.header__logo {
  width: 86px;
  height: 35px;
  display: flex;
  margin: 17px auto 22px;
  position: relative;
  z-index: 5;
  background: #fff;
  fill: #000000; }
  .header__logo .header__logo-icon-tablet {
    display: none; }
    @media (min-width: 768px) {
      .header__logo .header__logo-icon-tablet {
        display: block; } }
    @media (min-width: 920px) {
      .header__logo .header__logo-icon-tablet {
        display: none; } }
  @media (min-width: 768px) {
    .header__logo .header__logo-icon-mobile {
      display: none; } }
  .header__logo .header__logo-icon-desktop {
    display: none; }
    @media (min-width: 920px) {
      .header__logo .header__logo-icon-desktop {
        display: block; } }
  @media (min-width: 768px) {
    .header__logo {
      width: 114px;
      height: 46px; }
      .header__logo:hover, .header__logo:focus, .header__logo:focus-within {
        fill: rgba(0, 0, 0, 0.6); } }
  @media (min-width: 920px) {
    .header__logo {
      width: 149px;
      height: 72px; } }
  .header__logo:focus, .header__logo:focus-within {
    fill: rgba(0, 0, 0, 0.6); }
  .header__logo:active {
    fill: rgba(0, 0, 0, 0.3); }

.header__menu-button {
  position: absolute;
  z-index: 10;
  top: 15px;
  right: 17px; }

.header__link {
  color: #000000;
  fill: rgba(0, 0, 0, 0.2);
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  padding: 27px 27px 27px 76px;
  display: inline-block;
  width: 100%;
  position: relative;
  opacity: 1;
  visibility: visible;
  height: auto;
  z-index: 1;
  border-top: 1px solid #ececec; }
  .header__link:last-child {
    border-bottom: 1px solid #ececec; }
  @media (min-width: 768px) {
    .header__link:hover, .header__link:focus, .header__link:focus-within {
      color: rgba(0, 0, 0, 0.6);
      fill: #63d1bb; } }
  .header__link:focus, .header__link:focus-within {
    color: rgba(0, 0, 0, 0.6);
    fill: #63d1bb; }
  .header__link:active {
    color: rgba(0, 0, 0, 0.3);
    fill: rgba(99, 209, 187, 0.3); }

.header__icon {
  position: absolute;
  top: 50%;
  left: 30px;
  transform: translateY(-50%); }

.header--menu-close .header__link {
  -webkit-animation: move-out 0.3s ease-in forwards;
          animation: move-out 0.3s ease-in forwards; }
  .header--menu-close .header__link:nth-child(5) {
    -webkit-animation-delay: 0;
            animation-delay: 0; }
  .header--menu-close .header__link:nth-child(4) {
    -webkit-animation-delay: 0.1s;
            animation-delay: 0.1s; }
  .header--menu-close .header__link:nth-child(3) {
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s; }
  .header--menu-close .header__link:nth-child(2) {
    -webkit-animation-delay: 0.3s;
            animation-delay: 0.3s; }

.header--menu-open .header__link {
  -webkit-animation: move-in 0.3s ease-in backwards;
          animation: move-in 0.3s ease-in backwards; }
  .header--menu-open .header__link:nth-child(2) {
    -webkit-animation-delay: 0;
            animation-delay: 0; }
  .header--menu-open .header__link:nth-child(3) {
    -webkit-animation-delay: 0.1s;
            animation-delay: 0.1s; }
  .header--menu-open .header__link:nth-child(4) {
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s; }
  .header--menu-open .header__link:nth-child(5) {
    -webkit-animation-delay: 0.3s;
            animation-delay: 0.3s; }

.menu-button {
  width: 48px;
  height: 48px;
  display: grid;
  place-items: center;
  background: none; }
  @media (min-width: 768px) {
    .menu-button {
      display: none; } }

.menu-button__icon {
  width: 20px;
  height: 2px;
  display: block;
  background: #000000;
  position: relative; }
  .menu-button__icon::after, .menu-button__icon::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    background: #000000;
    left: 0;
    transition: transform 0.3s ease 0s; }
  .menu-button__icon::after {
    transform-origin: left bottom;
    bottom: 6px; }
  .menu-button__icon::before {
    transform-origin: left top;
    top: 6px; }

.menu-button[aria-expanded="true"] .menu-button__icon {
  height: 0; }
  .menu-button[aria-expanded="true"] .menu-button__icon::after {
    transform: translateX(2px) rotate(45deg); }
  .menu-button[aria-expanded="true"] .menu-button__icon::before {
    transform: translateX(2px) rotate(-45deg); }
